// COLOR DEFINITIONS

$color-orange: #ff6521;
$color-mint: #bbde95;
$color-laurel: #e7ebbe;
$color-deep-teal: #0f3f3f;

$color-white: #ffffff;
$color-bw-20: #F2F5F9;
$color-bw-40: #C3C8D0;
$color-bw-60: #9198A4;
$color-bw-80: #606A7A;
$color-bw-100: #0C1A30;

// TYPOGRAPHY

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-Regular.woff2') format('woff2'),
         url('../fonts/NeueMontreal-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-Bold.woff2') format('woff2'),
         url('../fonts/NeueMontreal-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-Italic.woff2') format('woff2'),
         url('../fonts/NeueMontreal-Italic.woff') format('woff');
	font-weight: normal;
	font-style: italic;
	font-display: swap;
}

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-BoldItalic.woff2') format('woff2'),
         url('../fonts/NeueMontreal-BoldItalic.woff') format('woff');
	font-weight: bold;
	font-style: italic;
	font-display: swap;
}

@font-face {
	font-family: 'Antonia H1';
	src: url('../fonts/AntoniaH1-Bold.woff2') format('woff2'),
         url('../fonts/AntoniaH1-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Antonia H1';
	src: url('../fonts/AntoniaH1-BoldItalic.woff2') format('woff2'),
         url('../fonts/AntoniaH1-BoldItalic.woff') format('woff');
	font-weight: bold;
	font-style: italic;
	font-display: swap;
}

$font-heading: 'Antonia H1', serif;
$font-body: 'Neue Montreal', sans-serif;

$font-size-base: 16px;

// LAYOUT

$wrap-max: 71.25rem;

// BASE STYLES

body {
  font-family: $font-body;
  font-size: $font-size-base;
  line-height: 1.5;
  color: $color-bw-100;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $font-heading;
  font-weight: bold;
}

h1 {
    font-size: 3rem;
    line-height: 1.2;
    font-weight: normal;
}

h2 {
    font-size: 2rem;
    line-height: 1.2;
}

p {
    font-size: 1.25rem;
    font-weight: normal;
}

ul {
    padding-left: 0;
    margin-left: 0;
    list-style-position: inside;
}

hr, hr.wp-block-separator {
    border: none;
    border-top: 0.5px solid $color-bw-40;
    margin: 0;
}

.wrap  {
  max-width: $wrap-max;
  margin: 0 auto;
}

.page {
    margin: 0;
}

// Stackable Component Fix

.play-optical-fix {
    svg {
        position: relative;
        left: 2px;
    }
}

// COMPONENT STYLES

// Buttons

.btn, .stk-button {
    background: $color-orange;
    color: $color-white;
    text-decoration: none;
    font-weight: normal;
    padding: 0.7rem 1.4rem;
    display: inline-block;

    &:visited {
        color: $color-white;
    }

    &:hover {
        box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.25);
        transition: 1s all;
        color: $color-white;
    }

    &.btn-outline {
        background: transparent;
        border: 2px solid $color-orange;
        color: $color-orange;


        &:visited {
            color: $color-orange;
        }

        &:hover {
            background: $color-orange;
            color: $color-white;
            transition: 1s all;
        }
    }

    &.btn-w-arrow {
        position: relative;
        padding-right: 3rem;

        &:after {
            content: "";
            display: inline-block;
            width: 16px;
            height: 12px;
            background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.17 7L8.59 10.59L10 12L16 6L10 0L8.59 1.41L12.17 5H0V7H12.17Z' fill='%23FFFFFF'/%3E%3C/svg%3E");
            background-size: contain;
            position: absolute;
            top: 50%;
            right: 1rem;
            transform: translateY(-50%);
        }
    } 
}

.site-header {
    background: transparent;
    margin: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    position: relative;
    z-index: 10000;
    height: 50px;
    @media screen and (max-width: 45.5rem) {
        gap: 1rem
    }

    .or-logo {
        width: 10rem;
        max-width: 10rem;
        height: auto;
        fill: $color-deep-teal;
    }

    .main-navigation {

        .menu-toggle {
            margin-left: auto;
            background: transparent;
            border: none;

            svg {
                width: 2rem;
                height: auto;
            }
        }

    }

    .nav-menu {
        gap: 0.5rem 2rem;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        @media screen and (max-width: 45.5rem) {
            display: none;
            background-color: $color-laurel;
            padding: 1.5rem;
            position: absolute;
            top: calc(100% + 2rem);
            right: 0;
            gap: 1rem;
            flex-direction: column;
            align-items: center;
        }

        .menu-item a {
            color: $color-bw-80;
            text-decoration: none;
            font-size: 1.25rem;
            transition: 1s color;

            &:visited {
                color: $color-bw-80;
            }

            &:hover {
                color: $color-orange;
                transition: 1s color;
            }

        }
    }

    .btn-cta {
        @media screen and (max-width: 45.5rem) {
            display: none;
        }
    }

}

.site-main {
    margin: 0 2rem;
    @media screen and (max-width: 45.5rem) {
        margin: 0;
    }

    .page-header {
        background: url('../img/webp/atf-bg.webp') no-repeat left top;
        background-color: $color-deep-teal;
        padding: 5rem 2rem;
        @media screen and (max-width: 45.5rem) {
            padding: 2rem 0;
        }

        .archive-header-columns {
            display: flex;
            align-items: center;
            gap: 4rem;
            @media screen and (max-width: 45.5rem) {
               flex-direction: column;
               gap: 1.5rem;
            }
            .ahc-left {
                flex-basis: 0;
                flex-grow: 1;
                color: $color-white;        
                @media screen and (max-width: 45.5rem) {
                    padding: 0 2rem;
                }

                .page-title {
                    font-family: $font-body;
                    font-weight: bold;
                    color: $color-mint;
                    text-transform: uppercase;
                    font-size: 1.25rem;
        
                    span {
                        color: $color-white;
                        font-size: 2.5rem;
                    }
                }

                h2 {
                    font-size: 4rem;
                    line-height: 1.2;
                    margin-top: 1rem;
                    margin-bottom: 2rem;
                }

                p {
                    font-size: 1.5rem;                }
            }

            .ahc-right {
                flex-basis: 0;
                flex-grow: 1;
            }
        }

    }
}

.site-footer {

    .footer-top {
        background: $color-deep-teal;
        color: $color-bw-40;
        padding: 5rem 2rem 0;
        margin: 0 2rem;
        @media screen and (max-width: 45.5rem) {
            padding: 2rem 2rem 0;
            margin: 0;
        }

        .wrap {
            display: flex;
            gap: 2rem;
            @media screen and (max-width: 45.5rem) {
                flex-direction: column-reverse;
            }
    
            .footer-text {
                flex-basis: 0;
                flex-grow: 1;

                .footer-logo {
                    width: 13.5rem;
                    max-width: 13.5rem;
                    height: auto;
                    margin-bottom: 1rem;
                    fill: $color-white;
                }
        
                .social-media-links {
        
                    a {     
                        svg {
                            width: 2rem;
                            height: auto;
                            fill: $color-mint;
                            margin-right: 1rem;
                            transition: 1s all;
                        }
        
                        &:hover {
                            svg {
                                fill: $color-orange;
                                transition: 1s all;
                            }
                        }
                    }        
                }
            }

            .footer-menu {
                flex-basis: 0;
                flex-grow: 1;
                display: flex;
                gap: 2rem;

                h2 {
                    font-family: $font-body;
                    font-weight: bold;
                    text-transform: uppercase;
                    color: $color-mint;
                    font-size: 1.25rem;
                    margin-top: 0;
                }

                ul {
                    list-style: none;
                    padding: 0;
                    margin: 0;

                    li {
                        margin-bottom: 1rem;

                        a {
                            color: $color-bw-20;
                            text-decoration: none;
                            font-size: 1.25rem;
                            transition: 1s color;

                            &:visited {
                                color: $color-bw-20;
                            }

                            &:hover {
                                color: $color-orange;
                                transition: 1s color;
                            }
                        }
                    }
                }
            }
        }

        .or-footer {
            width: 100%;
            max-width: $wrap-max;
            fill: $color-bw-20;
            margin: 0 auto;
            display: block ;
            opacity: 0.1;
            position: relative;
            bottom: -3px;
            @media screen and (max-width: 45.5rem) {
                margin-top: 1.5rem;
            }
        }

    }

    .footer-bottom {
        margin: 0 2rem;
        p {
            font-size: 0.75rem;
            color: $color-bw-60;
            margin: 0;
            padding: 0.5rem 0;
        }
    }

}

// FRONT PAGE MODS

.frontpage {

    .site-header {
        .or-logo {
            fill: $color-white;
        }

        .nav-menu {

            .menu-item a {
                color: $color-white;
                @media screen and (max-width: 45.5rem) {
                    color: $color-deep-teal;
                }
    
                &:visited {
                    color: $color-white;
                    @media screen and (max-width: 45.5rem) {
                        color: $color-deep-teal;
                    }
                }
    
                &:hover {
                    color: $color-orange;
                }
            }
        
        }



        .main-navigation {

            .menu-toggle {
    
                svg {
                    fill: $color-white;
                }
            }
    
        }
    }

    main  {
        margin: -115px 0 0;
    }
}

// HERO

h1 span {
    display: block;
    font-size: 5rem;
    @media screen and (max-width: 45.5rem) {
        font-size: 3rem;
    }
}

// PATTERN: AS SEEN IN

body .as-seen-in-row {
    justify-content: space-evenly;
}

// RESOURCES PAGE

main.full-width {
    margin: 0;

    .page-header {
        margin: 0 2rem;
        @media screen and (max-width: 45.5rem) {
            margin: 0;
        }
    }

    h2 {
        font-size: 2.5rem;
        line-height: 1.2;
        @media screen and (max-width: 75rem) {
            padding: 0 2rem;
            margin: 2rem 0;
        }
    }

    .wrap.query-container {
        @media screen and (max-width: 75rem) {
            padding: 0 2rem;
            margin: 2rem 0;
        }
        @media screen and (max-width: 45.5rem) {
            margin: 0;
            padding: 0;
        }
    }
}

.no-events {
    border: 1px solid $color-bw-40;
    padding: 1.5rem;

    p {
        margin: 0;
        text-align: center;
        color: $color-bw-60;
    }
}

// NEWSLETTER PAGE

.newsletter-optin-section h1 {
    font-family: $font-body;
    font-weight: bold;
}

// ARTICLES LOOP

.filter-container {
    padding: 3rem 0;
    display: flex;
    gap: 2rem;
    @media screen and (max-width: 45.5rem) {
        flex-direction: column;
        padding: 2rem;
        gap: 0.5rem;
    }

    .search-filter-style--search-text {
        width: calc( 2 * (100% / 3) + 2rem);
        @media screen and (max-width: 62rem) {
            width: 50%;
        }
        @media screen and (max-width: 45.5rem) {
            width: 100%;
        }

        .search-filter-input-text {
            border: 1px solid $color-bw-40;
            border-radius: 0;
            font-size: 1.25rem !important;
            padding: .5rem .5rem !important;
            color: $color-bw-100 !important;
        }
    }

    .search-filter-style--control-sort {
        width: calc( 100% / 3);
        @media screen and (max-width: 62rem) {
            width: 50%;
        }
        @media screen and (max-width: 45.5rem) {
            width: 100%;
        }

        .search-filter-component-combobox-base {
            border: 1px solid $color-bw-40;
            border-radius: 0;
            font-size: 1.25rem !important;
            padding: .5rem 0 0.5rem 1rem !important;
            color: $color-bw-100 !important;
        }

        .search-filter-component-combobox__listbox-toggle::before {
            display: none;
        }

    }

}

    .search-filter-component-combobox-base__listbox {
        border: 1px solid $color-bw-40 !important;
        border-radius: 0 !important;
        font-size: 1rem !important;

        li {
            padding: 1rem !important;
        }
    }

.articles-container {

    &.sf-articles {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
        @media screen and (max-width: 62rem) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media screen and (max-width: 45.5rem) {
            grid-template-columns: 1fr;
            padding: 0 2rem;
        }
    }

    &.events-carousel {
        article {
            flex-direction: row;
            padding: 0;
            gap: 0;
            @media screen and (max-width: 45.5rem) {
                flex-direction: column;
                border: none;
            }

            .post-thumbnail {
                aspect-ratio: 1 / 1;
                flex-basis: 0;
                flex-grow: 1;
                @media screen and (max-width: 45.5rem) {
                    flex-basis: initial;
                    flex-grow: initial;
                }
            }

            .event-text {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 1rem;
                flex-basis: 0;
                flex-grow: 2;
                padding: 1.5rem;
                @media screen and (max-width: 45.5rem) {
                    padding: 1.5rem 0;
                }

                h3 {
                    font-size: 1.75rem;
                    color: $color-bw-100;
                }

                .entry-content {

                    p {
                        color: $color-bw-60;
                    }
                }

                .btn {
                    margin-top: auto;
                }
            }

            &:hover {
                @media screen and (max-width: 45.5rem) {
                    border: none;
                    background-color: initial;
                    box-shadow: none;
                }
            }
        }
    }

    article {
        border: 1px solid $color-bw-40;
        padding: 1.5rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin-bottom: 0;
        transition: 1s all;
        position: relative;

        &:hover {
            border: 1px solid $color-bw-100;
            background-color: $color-bw-20;
            box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.25);
            transition: 1s all;

            .read-more-btn:after {
                opacity: 1;
                transition: 1s all;
            }
        }

        .post-thumbnail {
            aspect-ratio: 16 / 9;
            overflow: hidden;
            width: 100%; 

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .entry-title {
            margin: 0;
            a {
                color: $color-bw-100;
                text-decoration: none;
                font-size: 1.5rem;
            }
        }

        .entry-content {
            margin: 0;

            p {
                font-size: 1rem;
                margin: 0;
            }

        }

        hr {
            border: none;
            border-top: 0.5px solid $color-bw-40;
            margin: 0;
            margin-top: auto;
        }

        .read-more-btn {
            color: $color-orange;
            text-decoration: none;
            position: relative;

            &:after {
                content: "";
                display: inline-block;
                width: 16px;
                height: 12px;
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.17 7L8.59 10.59L10 12L16 6L10 0L8.59 1.41L12.17 5H0V7H12.17Z' fill='#ff6521'/%3E%3C/svg%3E");
                background-size: contain;
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                opacity: 0;
                transition: 1s all;
            }
        }

        .overlay-link {
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
        }

        .entry-footer {
            display: flex;
            justify-content: space-between;
            align-items: center;
                            color: $color-bw-60;

            .cat-links {
                display: flex;
                align-items: center;

                font-weight: bold;
                svg {
                    margin-right: 0.4rem;
                    fill: $color-deep-teal;
                }
            }
        }
    }
}

.load-more-btn {
    margin: 3rem 0;
    @media screen and (max-width: 45.5rem) {
        padding: 0 2rem;
        margin: 2rem 0;
    }
    button {
        display: block;
        width: 100% !important;
        text-align: right;
        color: $color-orange;
        border: none;
        padding: 1rem 0;
        position: relative;

        &:before {
            content: "";
            display: inline-block;
            aspect-ratio: 1 / 1;
            height: 100%;
            background-image: url('data:image/svg+xml,<svg width="12" height="17" viewBox="0 0 12 17" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5 12.67L1.41 9.09L0 10.5L6 16.5L12 10.5L10.59 9.09L7 12.67V0.5H5V12.67Z" fill="%23FF6521"/></svg>');
            background-size: 12px 17px;
            background-position: 50% 50%;
            background-repeat: no-repeat;
            position: absolute;
            top: 0;
            left: 0;
            border: 2px solid $color-orange;
        }

        &:hover {
            color: $color-orange !important;
            &:before {
                background-image: url('data:image/svg+xml,<svg width="12" height="17" viewBox="0 0 12 17" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5 12.67L1.41 9.09L0 10.5L6 16.5L12 10.5L10.59 9.09L7 12.67V0.5H5V12.67Z" fill="%23FFFFFF"/></svg>');
                background-color: $color-orange;
                transition: 1s all;
            }
        }

        .search-filter-icon {
            position: absolute;
            left: 19px;
        }

        &.search-filter-input-button--hidden {
            display: none;
        }
    }
}

// SINGLE POST

.single {

    background-image: url('../img/webp/sp-bg-laurel.webp');
    background-repeat: no-repeat;
    @media screen and (max-width: 45.5rem) {
        background-size: 100% auto;
    }

    .entry-header {
        width: 100%;
        max-width: 54rem;
        text-align: center;
        margin: 4rem auto;
        padding: 0 2rem;

        h1 {
            font-size: 4rem;
            margin-bottom: 1rem;
            color: $color-bw-100;
        }

        .entry-meta {
            font-size: 1.25rem;
            color: $color-bw-40;
            font-weight: bold;
            @media screen and (max-width: 70rem) {
                color: $color-bw-80;
            }

            svg {
                fill: $color-bw-40;
                @media screen and (max-width: 70rem) {
                    fill: $color-bw-80;
                }
            }
        }
    }
    .entry-content {
        width: 100%;
        max-width: 54rem;
        margin: 0 auto;
        padding: 0 2rem;
        
        p, li {
            font-size: 1.25rem;
        }
    }

    .post-navigation {
        width: 100%;
        max-width: 54rem;
        margin: 0 auto;
        padding: 0 2rem;
    }

    .comments-area {
        width: 100%;
        max-width: 54rem;
        margin: 0 auto;
        padding: 0 2rem;
    }

}

// TESTIMONIALS

.featured-testimonial {
    display: flex;
    gap: 4rem;
    align-items: center;
    margin: 5rem auto;
    @media screen and (max-width: 45.5rem) {
        padding: 0 2rem;
        margin: 2rem 0;
        gap: 2rem;
        flex-direction: column-reverse;
    }

    .testimonial-text {
        display: flex;
        flex-direction: column;
        flex-basis: 0;
        flex-grow: 1;

        blockquote {
            margin: 1rem 0;
            font-size: 2.25rem;
            font-family: $font-heading;
            color: $color-deep-teal;
            line-height: 1.3;
        }

        svg {
            width: 3rem;
            fill: $color-laurel;
            margin-bottom: 1rem;
        }

        .testimonial-author {
            font-size: 1.75rem;
            font-family: $font-heading;
            font-style: normal;
            color: $color-bw-100;
            display: block;
        }

        .author-website {
            font-family: $font-body;
            font-size: 1.5rem;
            color: $color-bw-80;
            display: block;
        }

    }

    .testimonial-media {
        flex-basis: 0;
        flex-grow: 1;
    }
}

.hr-wrap hr {
    margin: 5rem 0;
    @media screen and (max-width: 45.5rem) {
        padding: 0 2rem;
        margin: 3rem 2rem;
    }
}

// SINGLE TESTIMONIAL SHORTCODE

.single-testimonial {
    display: flex;
    align-items: center;
    gap: 2rem;
    margin: 3rem 0;
    @media screen and (max-width: 45.5rem) {
        padding: 0 2rem;
        margin: 2rem 0;
        flex-direction: column;
    }

    img {
        width: 6.25rem;
        height: 6.25rem;
        border-radius: 50%;
    }

    blockquote {
        font-size: 2rem;
        font-family: $font-heading;
        color: $color-deep-teal;
        line-height: 1.2;
        margin: 0;
        @media screen and (max-width: 45.5rem) {
            text-align: center;
        }
    }

    .testimonial-author-website {
        font-size: 1.25rem;
        text-transform: uppercase;
        color: $color-bw-80;
        margin-top: 0.5rem;
        display: block;
        @media screen and (max-width: 45.5rem) {
            text-align: center;
        }
    }

    svg {
        width: 3rem;
        height: 3rem;
        fill: $color-laurel;
        margin-left: auto;
        @media screen and (max-width: 45.5rem) {
            margin-right: auto;
        }
    }
}

// TESTIMONIALS CAROUSEL SHORTCODE

.testimonials-carousel {
    margin-bottom: 5rem;

    .single-testimonial {
        margin: 0 auto;
        padding: 2rem;
        justify-content: center;
        max-width: 34rem;

        svg {
            margin-bottom: 2rem;
        }

        .testimonial-author-website {
            font-weight: bold;
            color: $color-bw-40;
        }
    }

    .owl-dots {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        height: 6.5rem;

        .owl-dot {
            width: 4rem;
            height: 4rem;
            transition: 1s all;
            .dot-thumb {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                background-size: 100%;
                filter: grayscale(100%);
                transition: 1s all;
                display: block;
                cursor: pointer;
            }

            &.active {
                width: 6.25rem;
                height: 6.25rem;
                transition: 1s all;
                .dot-thumb {
                    filter: grayscale(0%);
                    transition: 1s all;
                }
            }
        }
    }
}


// KIT STYLING

.kit-custom-style {
    .formkit-container, .formkit-form {
        padding: 0 !important;
        box-shadow: none !important;
        background-color: transparent !important;    
    }
    .formkit-hero, .formkit-powered-by-convertkit-container {
        display: none !important;
    }

    .formkit-field {
        margin-bottom: 0.5rem !important;
        position: relative !important;

        input {
            background-color: $color-bw-20 !important;
            font-size: 1.25rem !important;
            padding: 1rem 3rem 1rem 1rem !important;
            color: $color-bw-100 !important;
            border: none !important;

        }

        &:after {
            content: "";
            display: inline-block;
            width: 18px;
            height: 14px;
            background-size: contain;
            position: absolute;
            top: 50%;
            right: 1rem;
            transform: translateY(-50%);
            background-image: url("data:image/svg+xml,%3Csvg width='18' height='14' viewBox='0 0 18 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.6667 13.6667H2.33341C1.41294 13.6667 0.666748 12.9205 0.666748 12V1.92751C0.705588 1.03547 1.44053 0.3325 2.33341 0.333344H15.6667C16.5872 0.333344 17.3334 1.07954 17.3334 2.00001V12C17.3334 12.9205 16.5872 13.6667 15.6667 13.6667ZM2.33341 3.55668V12H15.6667V3.55668L9.00008 8.00001L2.33341 3.55668ZM3.00008 2.00001L9.00008 6.00001L15.0001 2.00001H3.00008Z' fill='%239198A4'/%3E%3C/svg%3E%0A");
        }

    }

    .formkit-submit {
        background: $color-orange !important;
        color: $color-white !important;
        font-size: 1.25rem !important;
        border-radius: 0 !important;
        border: none !important;
        margin-bottom: 10px !important;

        span {
            padding: 1rem 3rem 1rem 1rem !important;
            text-align: left !important;
        }

        &:hover {
            box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.25);
            transition: 1s all;
        }

        &:after {
            content: "";
            display: inline-block;
            width: 16px;
            height: 12px;
            background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.17 7L8.59 10.59L10 12L16 6L10 0L8.59 1.41L12.17 5H0V7H12.17Z' fill='white'/%3E%3C/svg%3E");
            background-size: contain;
            position: absolute;
            top: 50%;
            right: 1rem;
            transform: translateY(-50%);
        }
    }

    &.kit-custom-style-atf {
        .formkit-field {
            width: 100% !important;
        }
    }

}

// OWL CAROUSEL

.owl-carousel {
        .owl-stage {
        display: flex;
    }

    .owl-item {
        display: flex;
        flex: 1 0 auto;
    }

    .owl-nav {
        max-width: $wrap-max;
        width: 100%;
        margin: 3rem auto;
        @media screen and (max-width: 75rem) {
            padding: 0 2rem;
            margin: 2rem 0;
        }

        button.owl-prev, button.owl-next {
            border: 2px solid $color-orange !important;
            width: 3.625rem;
            height: 3.625rem;
            border-radius: 0;
            margin-right: 0.5rem;
            transition: 1s all;
            svg {
                width: 16px;
                height: 12px;
                fill: $color-orange;
            }

            &:hover {
                background: $color-orange;
                transition: 1s all;
                svg {
                    fill: $color-white;
                }
            }
        }

        .disabled {
            opacity: 0.5;
            pointer-events: none;
        }
    }
}

.insights-container, .events-container {
    position: relative;

    .wrap-all-articles {
        position: absolute;
        bottom: 1.2rem;
        right: calc( (100vw - $wrap-max) / 2);
        width: fit-content;
        z-index: 999;
        @media screen and (max-width: 75rem) {
            right: 2rem;
        }

        a {
            color: $color-orange;
            text-decoration: none;
            font-size: 1.25rem;
            transition: 1s all;

            &:hover {
                text-decoration: underline;
            }
        }

        p {
            margin: 0;
            font-size: 1.25rem;
            color: $color-bw-80;
        }
    }
}