@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-Regular.woff2") format("woff2"), url("../fonts/NeueMontreal-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-Bold.woff2") format("woff2"), url("../fonts/NeueMontreal-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-Italic.woff2") format("woff2"), url("../fonts/NeueMontreal-Italic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-BoldItalic.woff2") format("woff2"), url("../fonts/NeueMontreal-BoldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Antonia H1";
  src: url("../fonts/AntoniaH1-Bold.woff2") format("woff2"), url("../fonts/AntoniaH1-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Antonia H1";
  src: url("../fonts/AntoniaH1-BoldItalic.woff2") format("woff2"), url("../fonts/AntoniaH1-BoldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}
body {
  font-family: "Neue Montreal", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #0C1A30;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Antonia H1", serif;
  font-weight: bold;
}

h1 {
  font-size: 3rem;
  line-height: 1.2;
  font-weight: normal;
}

h2 {
  font-size: 2rem;
  line-height: 1.2;
}

p {
  font-size: 1.25rem;
  font-weight: normal;
}

ul {
  padding-left: 0;
  margin-left: 0;
  list-style-position: inside;
}

hr, hr.wp-block-separator {
  border: none;
  border-top: 0.5px solid #C3C8D0;
  margin: 0;
}

.wrap {
  max-width: 71.25rem;
  margin: 0 auto;
}

.page {
  margin: 0;
}

.play-optical-fix svg {
  position: relative;
  left: 2px;
}

.btn, .stk-button {
  background: #ff6521;
  color: #ffffff;
  text-decoration: none;
  font-weight: normal;
  padding: 0.7rem 1.4rem;
  display: inline-block;
}
.btn:visited, .stk-button:visited {
  color: #ffffff;
}
.btn:hover, .stk-button:hover {
  box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.25);
  transition: 1s all;
  color: #ffffff;
}
.btn.btn-outline, .stk-button.btn-outline {
  background: transparent;
  border: 2px solid #ff6521;
  color: #ff6521;
}
.btn.btn-outline:visited, .stk-button.btn-outline:visited {
  color: #ff6521;
}
.btn.btn-outline:hover, .stk-button.btn-outline:hover {
  background: #ff6521;
  color: #ffffff;
  transition: 1s all;
}
.btn.btn-w-arrow, .stk-button.btn-w-arrow {
  position: relative;
  padding-right: 3rem;
}
.btn.btn-w-arrow:after, .stk-button.btn-w-arrow:after {
  content: "";
  display: inline-block;
  width: 16px;
  height: 12px;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.17 7L8.59 10.59L10 12L16 6L10 0L8.59 1.41L12.17 5H0V7H12.17Z' fill='%23FFFFFF'/%3E%3C/svg%3E");
  background-size: contain;
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
}

.site-header {
  background: transparent;
  margin: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  position: relative;
  z-index: 10000;
  height: 50px;
}
@media screen and (max-width: 45.5rem) {
  .site-header {
    gap: 1rem;
  }
}
.site-header .or-logo {
  width: 10rem;
  max-width: 10rem;
  height: auto;
  fill: #0f3f3f;
}
.site-header .main-navigation .menu-toggle {
  margin-left: auto;
  background: transparent;
  border: none;
}
.site-header .main-navigation .menu-toggle svg {
  width: 2rem;
  height: auto;
}
.site-header .nav-menu {
  gap: 0.5rem 2rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
@media screen and (max-width: 45.5rem) {
  .site-header .nav-menu {
    display: none;
    background-color: #e7ebbe;
    padding: 1.5rem;
    position: absolute;
    top: calc(100% + 2rem);
    right: 0;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
  }
}
.site-header .nav-menu .menu-item a {
  color: #606A7A;
  text-decoration: none;
  font-size: 1.25rem;
  transition: 1s color;
}
.site-header .nav-menu .menu-item a:visited {
  color: #606A7A;
}
.site-header .nav-menu .menu-item a:hover {
  color: #ff6521;
  transition: 1s color;
}
@media screen and (max-width: 45.5rem) {
  .site-header .btn-cta {
    display: none;
  }
}

.site-main {
  margin: 0 2rem;
}
@media screen and (max-width: 45.5rem) {
  .site-main {
    margin: 0;
  }
}
.site-main .page-header {
  background: url("../img/webp/atf-bg.webp") no-repeat left top;
  background-color: #0f3f3f;
  padding: 5rem 2rem;
}
@media screen and (max-width: 45.5rem) {
  .site-main .page-header {
    padding: 2rem 0;
  }
}
.site-main .page-header .archive-header-columns {
  display: flex;
  align-items: center;
  gap: 4rem;
}
@media screen and (max-width: 45.5rem) {
  .site-main .page-header .archive-header-columns {
    flex-direction: column;
    gap: 1.5rem;
  }
}
.site-main .page-header .archive-header-columns .ahc-left {
  flex-basis: 0;
  flex-grow: 1;
  color: #ffffff;
}
@media screen and (max-width: 45.5rem) {
  .site-main .page-header .archive-header-columns .ahc-left {
    padding: 0 2rem;
  }
}
.site-main .page-header .archive-header-columns .ahc-left .page-title {
  font-family: "Neue Montreal", sans-serif;
  font-weight: bold;
  color: #bbde95;
  text-transform: uppercase;
  font-size: 1.25rem;
}
.site-main .page-header .archive-header-columns .ahc-left .page-title span {
  color: #ffffff;
  font-size: 2.5rem;
}
.site-main .page-header .archive-header-columns .ahc-left h2 {
  font-size: 4rem;
  line-height: 1.2;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.site-main .page-header .archive-header-columns .ahc-left p {
  font-size: 1.5rem;
}
.site-main .page-header .archive-header-columns .ahc-right {
  flex-basis: 0;
  flex-grow: 1;
}

.site-footer .footer-top {
  background: #0f3f3f;
  color: #C3C8D0;
  padding: 5rem 2rem 0;
  margin: 0 2rem;
}
@media screen and (max-width: 45.5rem) {
  .site-footer .footer-top {
    padding: 2rem 2rem 0;
    margin: 0;
  }
}
.site-footer .footer-top .wrap {
  display: flex;
  gap: 2rem;
}
@media screen and (max-width: 45.5rem) {
  .site-footer .footer-top .wrap {
    flex-direction: column-reverse;
  }
}
.site-footer .footer-top .wrap .footer-text {
  flex-basis: 0;
  flex-grow: 1;
}
.site-footer .footer-top .wrap .footer-text .footer-logo {
  width: 13.5rem;
  max-width: 13.5rem;
  height: auto;
  margin-bottom: 1rem;
  fill: #ffffff;
}
.site-footer .footer-top .wrap .footer-text .social-media-links a svg {
  width: 2rem;
  height: auto;
  fill: #bbde95;
  margin-right: 1rem;
