  position: relative;
  bottom: -3px;
}
@media screen and (max-width: 45.5rem) {
  .site-footer .footer-top .or-footer {
    margin-top: 1.5rem;
  }
}
.site-footer .footer-bottom {
  margin: 0 2rem;
}
.site-footer .footer-bottom p {
  font-size: 0.75rem;
  color: #9198A4;
  margin: 0;
  padding: 0.5rem 0;
}

.frontpage .site-header .or-logo {
  fill: #ffffff;
}
.frontpage .site-header .nav-menu .menu-item a {
  color: #ffffff;
}
@media screen and (max-width: 45.5rem) {
  .frontpage .site-header .nav-menu .menu-item a {
    color: #0f3f3f;
  }
}
.frontpage .site-header .nav-menu .menu-item a:visited {
  color: #ffffff;
}
@media screen and (max-width: 45.5rem) {
  .frontpage .site-header .nav-menu .menu-item a:visited {
    color: #0f3f3f;
  }
}
.frontpage .site-header .nav-menu .menu-item a:hover {
  color: #ff6521;
}
.frontpage .site-header .main-navigation .menu-toggle svg {
  fill: #ffffff;
}
.frontpage main {
  margin: -115px 0 0;
}

h1 span {
  display: block;
  font-size: 5rem;
}
@media screen and (max-width: 45.5rem) {
  h1 span {
    font-size: 3rem;
  }
}

body .as-seen-in-row {
  justify-content: space-evenly;
}

main.full-width {
  margin: 0;
}
main.full-width .page-header {
  margin: 0 2rem;
}
@media screen and (max-width: 45.5rem) {
  main.full-width .page-header {
    margin: 0;
  }
}
main.full-width h2 {
  font-size: 2.5rem;
  line-height: 1.2;
}
@media screen and (max-width: 75rem) {
  main.full-width h2 {
    padding: 0 2rem;
    margin: 2rem 0;
  }
}
@media screen and (max-width: 75rem) {
