  }
}

.no-events {
  border: 1px solid #C3C8D0;
  padding: 1.5rem;
}
.no-events p {
  margin: 0;
  text-align: center;
  color: #9198A4;
}

.newsletter-optin-section h1 {
  font-family: "Neue Montreal", sans-serif;
  font-weight: bold;
}

.filter-container {
  padding: 3rem 0;
  display: flex;
  gap: 2rem;
}
@media screen and (max-width: 45.5rem) {
  .filter-container {
    flex-direction: column;
    padding: 2rem;
    gap: 0.5rem;
  }
}
.filter-container .search-filter-style--search-text {
  width: calc(66.6666666667% + 2rem);
}
@media screen and (max-width: 62rem) {
  .filter-container .search-filter-style--search-text {
    width: 50%;
  }
}
@media screen and (max-width: 45.5rem) {
  .filter-container .search-filter-style--search-text {
    width: 100%;
  }
}
.filter-container .search-filter-style--search-text .search-filter-input-text {
  border: 1px solid #C3C8D0;
  border-radius: 0;
  font-size: 1.25rem !important;
  padding: 0.5rem 0.5rem !important;
  color: #0C1A30 !important;
}
.filter-container .search-filter-style--control-sort {
  width: 33.3333333333%;
}
@media screen and (max-width: 62rem) {
  .filter-container .search-filter-style--control-sort {
    width: 50%;
  }
}
@media screen and (max-width: 45.5rem) {
  .filter-container .search-filter-style--control-sort {
    width: 100%;
  }
}
.filter-container .search-filter-style--control-sort .search-filter-component-combobox-base {
  border: 1px solid #C3C8D0;
  border-radius: 0;
  font-size: 1.25rem !important;
  padding: 0.5rem 0 0.5rem 1rem !important;
  color: #0C1A30 !important;
}
.filter-container .search-filter-style--control-sort .search-filter-component-combobox__listbox-toggle::before {
  display: none;
}

.search-filter-component-combobox-base__listbox {
  border: 1px solid #C3C8D0 !important;
  border-radius: 0 !important;
  font-size: 1rem !important;
}
.search-filter-component-combobox-base__listbox li {
  padding: 1rem !important;
}

.articles-container.sf-articles {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media screen and (max-width: 62rem) {
  .articles-container.sf-articles {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 45.5rem) {
  .articles-container.sf-articles {
    grid-template-columns: 1fr;
    padding: 0 2rem;
  }
}
.articles-container.events-carousel article {
