  flex-basis: 0;
  flex-grow: 1;
}
@media screen and (max-width: 45.5rem) {
  .articles-container.events-carousel article .post-thumbnail {
    flex-basis: initial;
    flex-grow: initial;
  }
}
.articles-container.events-carousel article .event-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  flex-basis: 0;
  flex-grow: 2;
  padding: 1.5rem;
}
@media screen and (max-width: 45.5rem) {
  .articles-container.events-carousel article .event-text {
    padding: 1.5rem 0;
  }
}
.articles-container.events-carousel article .event-text h3 {
  font-size: 1.75rem;
  color: #0C1A30;
}
.articles-container.events-carousel article .event-text .entry-content p {
  color: #9198A4;
}
.articles-container.events-carousel article .event-text .btn {
  margin-top: auto;
}
@media screen and (max-width: 45.5rem) {
  .articles-container.events-carousel article:hover {
    border: none;
    background-color: initial;
    box-shadow: none;
  }
}
.articles-container article {
  border: 1px solid #C3C8D0;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 0;
  transition: 1s all;
  position: relative;
}
.articles-container article:hover {
  border: 1px solid #0C1A30;
  background-color: #F2F5F9;
  box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.25);
  transition: 1s all;
}
.articles-container article:hover .read-more-btn:after {
  opacity: 1;
  transition: 1s all;
}
.articles-container article .post-thumbnail {
  aspect-ratio: 16/9;
  overflow: hidden;
  width: 100%;
}
.articles-container article .post-thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.articles-container article .entry-title {
  margin: 0;
}
.articles-container article .entry-title a {
  color: #0C1A30;
  text-decoration: none;
  font-size: 1.5rem;
}
.articles-container article .entry-content {
  margin: 0;
}
.articles-container article .entry-content p {
  font-size: 1rem;
  margin: 0;
}
.articles-container article hr {
  border: none;
  border-top: 0.5px solid #C3C8D0;
  margin: 0;
  margin-top: auto;
}
.articles-container article .read-more-btn {
  color: #ff6521;
  text-decoration: none;
  position: relative;
}
.articles-container article .read-more-btn:after {
  content: "";
  display: inline-block;
  width: 16px;
  height: 12px;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.17 7L8.59 10.59L10 12L16 6L10 0L8.59 1.41L12.17 5H0V7H12.17Z' fill='#ff6521'/%3E%3C/svg%3E");
  background-size: contain;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  opacity: 0;
  transition: 1s all;
}
.articles-container article .overlay-link {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.articles-container article .entry-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #9198A4;
}
.articles-container article .entry-footer .cat-links {
  display: flex;
  align-items: center;
  font-weight: bold;
}
.articles-container article .entry-footer .cat-links svg {
  margin-right: 0.4rem;
  fill: #0f3f3f;
}

.load-more-btn {
  margin: 3rem 0;
}
@media screen and (max-width: 45.5rem) {
  .load-more-btn {
