    margin: 2rem 0;
  }
}
.load-more-btn button {
  display: block;
  width: 100% !important;
  text-align: right;
  color: #ff6521;
  border: none;
  padding: 1rem 0;
  position: relative;
}
.load-more-btn button:before {
  content: "";
  display: inline-block;
  aspect-ratio: 1/1;
  height: 100%;
  background-image: url('data:image/svg+xml,<svg width="12" height="17" viewBox="0 0 12 17" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5 12.67L1.41 9.09L0 10.5L6 16.5L12 10.5L10.59 9.09L7 12.67V0.5H5V12.67Z" fill="%23FF6521"/></svg>');
  background-size: 12px 17px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid #ff6521;
}
.load-more-btn button:hover {
  color: #ff6521 !important;
}
.load-more-btn button:hover:before {
  background-image: url('data:image/svg+xml,<svg width="12" height="17" viewBox="0 0 12 17" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5 12.67L1.41 9.09L0 10.5L6 16.5L12 10.5L10.59 9.09L7 12.67V0.5H5V12.67Z" fill="%23FFFFFF"/></svg>');
  background-color: #ff6521;
  transition: 1s all;
}
.load-more-btn button .search-filter-icon {
  position: absolute;
  left: 19px;
}
.load-more-btn button.search-filter-input-button--hidden {
  display: none;
}

